import React from "react";
import { MDBDataTable } from "mdbreact";
export default function BTable(props) {
  return (
    <>
      <MDBDataTable
        id="divToPrint"
        className="text-dark black-text p-2 strong"
        scorllable
        responsive
        striped
        theadColor="dark"
        small
        noBottomColumns
        entries={14}
        hover
        data={props.data}
      />
    </>
  );
}
